












export default {
	data() {
		return {
			title:'',
			strings:'',
			published_at:'',
			image:""
		};
	},
onLoad:function(e){
		console.log(e.newsid);
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.title=res.data.title;
				this.published_at=res.data.published_at;
				this.strings=res.data.content;
				this.image=res.data.cover;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
